.sidebar {
    display: none;
}

.sidebar.open {
    position: fixed;
    top:0;
    left:0;
    width: 317px;
    height: 100%;
    display: block;
    padding: 20px;
    background: #FFFFFF;
    z-index: 2;
    .close{
        display:inline;
    }
}

.background {
    background-color:#000000;
    opacity: 0.33;
    width: 100%;
    height: 100%;
    position:absolute;
    top: 0;
    left: 0;
    z-index: 2
}

.nav {
    display: flex;
    flex-direction: column;
    gap: 45px;
    align-items: flex-start;
    justify-content: center;
    padding-top:22px;
    padding-left:70px;
}

.nav a {
    font-size: 22px;
    font-weight: 600;
    text-decoration: none;
}

.nav a:hover {
    color:#58acff;
}

.nav a.active {
    color:#58acff;
    text-decoration:underline;
}
.close {
    display: none;
}

.close img {
    position: absolute;
    display: flex;
    right: 24px;
    top: 16px;
    flex-direction: row-reverse;
}
